import { useState, useRef } from "react";
import { handleNumberChange, handleNumberInput, formatExp } from "../utils";

export default function Stopwatch() {
  const [time, setTime] = useState(0);
  const [isRunning, setIsRunning] = useState(false);
  const [expBefore, setExpBefore] = useState(0);
  const [expAfter, setExpAfter] = useState(0);
  const intervalRef = useRef(null);
  const [updateLogVisible, setUpdateLogVisible] = useState(false);

  const updateLog = `
  - 2025-01-30 : 
    스톱워치 추가
  `;
  const toggleUpdateLog = () => {
    setUpdateLogVisible((prev) => !prev);
  };

  const startStopwatch = () => {
    if (!isRunning) {
      setIsRunning(true);
      intervalRef.current = setInterval(() => {
        setTime((prevTime) => prevTime + 10);
      }, 10);
    }
  };

  const stopStopwatch = () => {
    setIsRunning(false);
    clearInterval(intervalRef.current);
  };

  const resetStopwatch = () => {
    setIsRunning(false);
    clearInterval(intervalRef.current);
    setTime(0);
    setExpBefore(0);
    setExpAfter(0);
  };

  const adjustTime = (amount) => {
    setTime((prevTime) => Math.max(prevTime + amount, 0));
  };

  const formatTime = (milliseconds) => {
    const minutes = Math.floor(milliseconds / 60000);
    const seconds = Math.floor((milliseconds % 60000) / 1000);
    const ms = (milliseconds % 1000) / 10;
    return `${minutes.toString().padStart(2, "0")}:${seconds
      .toString()
      .padStart(2, "0")}:${ms.toString().padStart(2, "0")}`;
  };

  const elapsedMinutes = time / 60000;
  const expGain = expAfter - expBefore;
  const expPer10Min = elapsedMinutes > 0 ? (expGain / elapsedMinutes) * 10 : 0;
  const expPerHour = expPer10Min * 6;

  return (
    <div className="app">
      <div className="header">
        <h1 className="title">스톱워치</h1>
        <button className="update-log-button" onClick={toggleUpdateLog}>
          업데이트 내역
        </button>
      </div>
      {updateLogVisible && (
          <div className="update-log">
            <h3>업데이트 내역</h3>
            <pre>{updateLog}</pre>
          </div>
        )}
        <div className="stopwatch-container">
      <div className="stopwatch-display">{formatTime(time)}</div>
      <div className="region-buttons">
        <button onClick={startStopwatch} disabled={isRunning}>시작</button>
        <button onClick={stopStopwatch} disabled={!isRunning}>정지</button>
        <button onClick={resetStopwatch}>리셋</button>
        <button onClick={() => adjustTime(60000)}>1분 추가</button>
        <button onClick={() => adjustTime(-60000)}>1분 제거</button>
        <button onClick={() => adjustTime(1000)}>1초 추가</button>
        <button onClick={() => adjustTime(-1000)}>1초 제거</button>
      </div>
      <hr></hr>
      <h2>경험치 측정</h2>
      <div className="exp-inputs">
        <label>측정 전 경험치:</label>
        <input 
            type="text" 
            value={expBefore.toLocaleString()} 
            onChange={(e) => handleNumberChange(e, setExpBefore)} 
            onInput={handleNumberInput} 
          />
        <label> 측정 후 경험치:</label>
          <input 
            type="text" 
            value={expAfter.toLocaleString()} 
            onChange={(e) => handleNumberChange(e, setExpAfter)} 
            onInput={handleNumberInput} 
          />
        </div>
        <div className="results">
          <p>경험치 증가량: {formatExp(expGain)}</p>
          <p>10분 당 경험치: {formatExp(expPer10Min)}</p>
          <p>1시간 당 경험치: {formatExp(expPerHour)}</p>
        </div>
      </div>
    </div>
  );
}
