import React, { useState } from "react";
import { buyeo } from "../data/buyeo.js";
import { zodiac12 } from "../data/zodiac12.js";
import { northstep } from "../data/northstep.js";
import { japan } from "../data/japan.js";
import { underpalace } from "../data/underpalace.js";
import { northchina } from "../data/northchina.js";
import { icebandit } from "../data/icebandit.js";
import { formatNumber, calculateStats } from "../utils.js";


const MonsterInfo = () => {

  const updateLog = `
  - 2025-01-28 : 
    무사의방 난이도 수정
    방어력 하향조정(기존: -80, 변경: -30)
    체력 상향조정(2배)
  - 2025-01-29 :
    신극지방 수정
  - 2025-01/30 :
    생명의기원, 신령의기원 1킬 마나량 추가
  `;

  const toggleUpdateLog = () => {
    setUpdateLogVisible((prev) => !prev);
  };

  //monster INFO
  const [updateLogVisible, setUpdateLogVisible] = useState(false);
  const [selectedRegion, setSelectedRegion] = useState(null);
  const [selectedDungeon, setSelectedDungeon] = useState(null);
  //

  const [debuffs, setDebuffs] = useState({
    curse: false,
    soul: false,
    magicSlow: false,
    rupture: false,
    blade: false,
    intangible: false,
    jinbaekhor: false,
  });

  const [skills, setSkills] = useState({
    skill1: false, //필건백1
    skill2: false, //동귀2
    skill3: false, //어검술3
    skill4: false, //쇄초4
    skill5: false, //헬파5
    skill6: false, //지폭6
    skill7: false, //백검7
    skill8: false, //생명의기원
    skill9: false, //신령의기원

  });

  const [heals, setHeals] = useState({
    heal1: false, //생명의기원
    heal2: false, //신령의기원
  });

  const toggleDebuff = (debuff) => {
    setDebuffs((prev) => ({ ...prev, [debuff]: !prev[debuff] }));
  };

  const toggleSkill = (skill) => {
    setSkills((prev) => {
      let newSkills = {
        skill1: false,
        skill2: false,
        skill3: false,
        skill4: false,
        skill5: false,
        skill6: false,
        skill7: false,
      };  
      // 클릭한 스킬만 활성화
      newSkills[skill] = !prev[skill];
  
      return newSkills;
    });
  };

  const toggleHeal = (heal) => {
    setHeals((prev) => {
      let newHeals = {
        heal1 : false,
        heal2 : false,
      }
      newHeals[heal] = !prev[heal];

      return newHeals;
    })
  }



  const regions = [
    { name: "부여", data: buyeo },
    { name: "12지신", data: zodiac12 },
    { name: "산적/극지방", data: icebandit },
    { name: "북방대초원", data: northstep },
    { name: "일본", data: japan },
    { name: "용궁", data: underpalace },
    { name: "중국북부", data: northchina },
  ];

  const handleRegionClick = (regionIndex) => {
    setSelectedRegion(regionIndex);
    setSelectedDungeon(null);
  };

  const handleDungeonClick = (dungeonIndex) => {
    setSelectedDungeon(dungeonIndex);
  };


  return (
    <div className="app">
      <div className="header">
      <h1 className="title">몬스터 정보</h1>
        <button className="update-log-button" onClick={toggleUpdateLog}>
          {updateLogVisible ? "업데이트 내역" : "업데이트 내역"}
        </button>
      </div>
      {updateLogVisible && (
        <div className="update-log">
          <h3>업데이트 내역</h3>
          <pre>{updateLog}</pre>
        </div>
      )}

      <div className="dungeon-buttons">
        {regions.map((region, regionIndex) => (
          <button
            key={regionIndex}
            className={`dungeon-button ${selectedRegion === regionIndex ? "active" : ""
              }`}
            onClick={() => handleRegionClick(regionIndex)}
          >
            {region.name}
          </button>
        ))}
      </div>

      {selectedRegion !== null && (
        <div className="dungeon-buttons">
          {regions[selectedRegion].data.regions.map((region, regionIndex) => (
            <button
              key={regionIndex}
              className={`dungeon-button ${selectedDungeon === regionIndex ? "active" : ""
                }`}
              onClick={() => handleDungeonClick(regionIndex)}
            >
              {region.name}
            </button>
          ))}
        </div>
      )}

      <div className="debuffs">
        <label>
          <input
            type="checkbox"
            checked={debuffs.curse}
            onChange={() => toggleDebuff("curse")}
          />
          저주
        </label>
        <label>
          <input
            type="checkbox"
            checked={debuffs.soul}
            onChange={() => toggleDebuff("soul")}
          />
          혼마술
        </label>
        <label>
          <input
            type="checkbox"
            checked={debuffs.magicSlow}
            onChange={() => toggleDebuff("magicSlow")}
          />
          마기지체
        </label>
        <label>
          <input
            type="checkbox"
            checked={debuffs.rupture}
            onChange={() => toggleDebuff("rupture")}
          />
          파력무참진
        </label>
        <label>
          <input
            type="checkbox"
            checked={debuffs.blade}
            onChange={() => toggleDebuff("blade")}
          />
          이기어검술
        </label>
        <label>
          <input
            type="checkbox"
            checked={debuffs.intangible}
            onChange={() => toggleDebuff("intangible")}
          />
          무형술
        </label>
        <label>
          <input
            type="checkbox"
            checked={debuffs.jinbaekhor}
            onChange={() => toggleDebuff("jinbaekhor")}
          />
          진백호령
        </label>
      </div>

      <div className="debuffs">
        <label>
          <input
            type="checkbox"
            checked={skills.skill1}
            onChange={() => toggleSkill("skill1")}
          />
          필/건/백
        </label>
        <label>
          <input
            type="checkbox"
            checked={skills.skill2}
            onChange={() => toggleSkill("skill2")}
          />
          동귀어진
        </label>
        <label>
          <input
            type="checkbox"
            checked={skills.skill3}
            onChange={() => toggleSkill("skill3")}
          />
          어검술
        </label>
        <label>
          <input
            type="checkbox"
            checked={skills.skill4}
            onChange={() => toggleSkill("skill4")}
          />
          쇄/초혼비무
        </label>
        <label>
          <input
            type="checkbox"
            checked={skills.skill5}
            onChange={() => toggleSkill("skill5")}
          />
          헬파이어
        </label>
        <label>
          <input
            type="checkbox"
            checked={skills.skill6}
            onChange={() => toggleSkill("skill6")}
          />
          지폭지술
        </label>
        <label>
          <input
            type="checkbox"
            checked={skills.skill7}
            onChange={() => toggleSkill("skill7")}
          />
          백호검무
        </label>
        <label> | </label>
        <label>
          <input
            type="checkbox"
            checked={heals.heal1}
            onChange={() => toggleHeal("heal1")}
          />
          생명의기원
        </label>
        <label>
          <input
            type="checkbox"
            checked={heals.heal2}
            onChange={() => toggleHeal("heal2")}
          />
          신령의기원
        </label>
        <hr></hr>
      </div>

      {selectedRegion !== null && selectedDungeon !== null && (
        <div className="monster-info">
          {regions[selectedRegion].data.regions[selectedDungeon].monsters.map(
            (monster, monsterIndex) => {
              //console.log(regions[selectedRegion].data.code);
              const stats = calculateStats(
                monster.health,
                monster.defense,
                debuffs,
                skills,
                heals
              );
              return (
                <div className="monster-card" key={monsterIndex}>
                  <h4>{monster.name}</h4>
                  <img
                    src={require(`../data/${regions[selectedRegion].data.code}/${monster.image}`)}
                    alt={monster.name}
                    className="monster-image"
                  />
                  <ul>
                    <li>최대 체력: {formatNumber(stats.maxHealth)}</li>
                    <li>방어도: {formatNumber(stats.adjustedDefense)}</li>
                    <li>경험치: {formatNumber(monster.exp)}</li>
                    <li>필요 피해량: {formatNumber(stats.appliedHealthBeforeSkills)}</li>
                    <hr></hr>
                    <li>필요 체력/마력(1킬): {formatNumber(stats.appliedHealth)}</li>
                  </ul>
                </div>
              );
            }
          )}
        </div>
      )}
    </div>
  );
};

export default MonsterInfo;
