import React, { useState, useEffect } from "react";
import { experienceData } from "../data/expData";
import { handleNumberChange, handleNumberInput, formatExp, calculateExp, calculateTime } from "../utils";

const Calculator = () => {
  const [mode, setMode] = useState("격수");
  const [currentHP, setCurrentHP] = useState(0);
  const [targetHP, setTargetHP] = useState(0);
  const [currentMP, setCurrentMP] = useState(0);
  const [targetMP, setTargetMP] = useState(0);
  const [totalExp, setTotalExp] = useState({ hp: 0, mp: 0 });
  const [timeEstimates, setTimeEstimates] = useState({ hours: 0, laps: 0 });
  const [expPerHour, setExpPerHour] = useState(0);
  const [expPerLap, setExpPerLap] = useState(0);
  const [updateLogVisible, setUpdateLogVisible] = useState(false);

  const updateLog = `
  - 2025-01-29 : 
    체/마 계산기 추가
  - 2025-01-30 :
    바퀴/시간 오류 수정
    결과값 실시간 반영
  `;

  const toggleUpdateLog = () => {
    setUpdateLogVisible((prev) => !prev);
  };

  // 경험치 자동 계산
  useEffect(() => {
    const result = calculateExp(mode, currentHP, targetHP, currentMP, targetMP, experienceData);
    setTotalExp(result);
  }, [mode, currentHP, targetHP, currentMP, targetMP]);

  // 예상 시간 자동 계산
  useEffect(() => {
    const result = calculateTime(totalExp, expPerHour, expPerLap);
    setTimeEstimates(result);
  }, [totalExp, expPerHour, expPerLap]);

  return (
    <>
      <div className="app">
        <div className="header">
          <h1 className="title">경험치 계산기</h1>
          <button className="update-log-button" onClick={toggleUpdateLog}>
            {updateLogVisible ? "업데이트 내역" : "업데이트 내역"}
          </button>
        </div>
        {updateLogVisible && (
          <div className="update-log">
            <h3>업데이트 내역</h3>
            <pre>{updateLog}</pre>
          </div>
        )}
        <div className="calculator-container">
          <div className="mode-buttons">
            <button onClick={() => setMode("격수")} className={mode === "격수" ? "active" : ""}>
              전사/도적
            </button>
            <button onClick={() => setMode("비격수")} className={mode === "비격수" ? "active" : ""}>
              도사/주술사
            </button>
          </div>

          <div className="input-grid">
            <label>현재 체력</label><label>목표 체력</label>
            <input
              type="text"
              placeholder="현재 체력"
              value={currentHP.toLocaleString()}
              onChange={(e) => handleNumberChange(e, setCurrentHP)}
              onInput={handleNumberInput}
            />
            <input
              type="text"
              placeholder="목표 체력"
              value={targetHP.toLocaleString()}
              onChange={(e) => handleNumberChange(e, setTargetHP)}
              onInput={handleNumberInput}
            />

            <label>현재 마력</label><label>목표 마력</label>
            <input
              type="text"
              placeholder="현재 마력"
              value={currentMP.toLocaleString()}
              onChange={(e) => handleNumberChange(e, setCurrentMP)}
              onInput={handleNumberInput}
            />
            <input
              type="text"
              placeholder="목표 마력"
              value={targetMP.toLocaleString()}
              onChange={(e) => handleNumberChange(e, setTargetMP)}
              onInput={handleNumberInput}
            />
          </div>


          <div className="exp-inputs">
            <label>시간 당 경험치 (억)</label>
            <input
              type="number"
              placeholder="시간 당 경험치"
              value={expPerHour || ""}
              onChange={(e) => setExpPerHour(Number(e.target.value))}
            />
            <label>바퀴 당 경험치 (억)</label>
            <input
              type="number"
              placeholder="바퀴 당 경험치"
              value={expPerLap || ""}
              onChange={(e) => setExpPerLap(Number(e.target.value))}
            />
          </div>

          <hr />

          <div className="exp-result">필요 체력 경험치: {formatExp(totalExp.hp)}</div>
          <div className="exp-result">필요 마력 경험치: {formatExp(totalExp.mp)}</div>
          <div className="exp-result">필요 경험치: {formatExp(totalExp.hp + totalExp.mp)}</div>

          <br />

          <div className="exp-result">
            예상 시간: {Math.floor(timeEstimates.hours)}시간 {Math.round((timeEstimates.hours % 1) * 60)}분
          </div>
          <div className="exp-result">예상 바퀴 수: {Math.ceil(timeEstimates.laps)}</div>
        </div>
      </div>
    </>
  );
};

export default Calculator;
