// 숫자를 콤마로 구분해 포맷팅
export const formatNumber = (number) => {
    return number.toLocaleString();
  };
  
  // 스탯 계산 로직
  export const calculateStats = (baseHealth, defense, debuffs, skills, heals) => {
    const { curse, soul, magicSlow, rupture, blade, intangible, jinbaekhor } = debuffs;
  
    const adjustedDefense =
      defense +
      (curse ? 30 : 0) +
      (soul ? 50 : 0) +
      (magicSlow ? 80 : 0) +
      (blade ? 20 : 0) +
      (intangible ? 40 : 0);
  
    let damageReduction;
    if (adjustedDefense >= -95) {
      damageReduction = Math.max(-95, adjustedDefense) * 0.01;
    } else {
      damageReduction = -0.95 + (adjustedDefense + 95) * 0.005;
    }
  
    const minHealth = baseHealth * 0.8;
    const maxHealth = baseHealth * 1.2;
    const defenseHealth = maxHealth / (1 + damageReduction);
    let appliedHealth = rupture ? defenseHealth / 2 : defenseHealth;
  
    let appliedHealthBeforeSkills = appliedHealth;
  
    if (jinbaekhor) {
      appliedHealth *= 0.667;
    }
  
    if (skills.skill2) {
      appliedHealth *= 0.5;
    } else if (skills.skill3) {
      appliedHealth *= 2;
    }
    else if (skills.skill4) {
      appliedHealth *= 1/3;
    }
    else if (skills.skill5) {
      appliedHealth -= 10000;
      if (appliedHealth < 0) {
        appliedHealth = 0;
      } else {
        appliedHealth *= 2/3;
      }
    } else if (skills.skill6) {
      appliedHealth *= 2/3;
    }  else if (skills.skill7) {
      appliedHealth -= 20000;
      if (appliedHealth < 0) {
        appliedHealth = 0;
      }
    } 

    if (heals.heal1) {
      if(skills.skill1){
        appliedHealth = appliedHealth/2 -15000;
        if(appliedHealth < 0){
          appliedHealth = 0;
        }
      }
      else if(skills.skill2){
        appliedHealth = appliedHealth-15000;
        if(appliedHealth < 0){
          appliedHealth = 0;
        }
      }
      else if(skills.skill3){
        appliedHealth = appliedHealth/2 -15000;
        if(appliedHealth < 0){
          appliedHealth = 0;
        }
      }
      else if(skills.skill4){
        appliedHealth = appliedHealth*3/4-15000;
        if(appliedHealth < 0){
          appliedHealth = 0;
        }
      }
      else if(skills.skill7){
        appliedHealth = appliedHealth/2 -15000;
        if(appliedHealth < 0){
          appliedHealth = 0;
        }
      }
      appliedHealth = appliedHealth*100/9;
    }
    else if (heals.heal2) {
      if(skills.skill1){
        appliedHealth = appliedHealth/2 -30000;
        if(appliedHealth < 0){
          appliedHealth = 0;
        }
      }
      else if(skills.skill2){
        appliedHealth = appliedHealth-30000;
        if(appliedHealth < 0){
          appliedHealth = 0;
        }
      }
      else if(skills.skill3){
        appliedHealth = appliedHealth/2 -30000;
        if(appliedHealth < 0){
          appliedHealth = 0;
        }
      }
      else if(skills.skill4){
        appliedHealth = appliedHealth*3/4-30000;
        if(appliedHealth < 0){
          appliedHealth = 0;
        }
      }
      else if(skills.skill7){
        appliedHealth = appliedHealth/2 -30000;
        if(appliedHealth < 0){
          appliedHealth = 0;
        }
      }
      appliedHealth = appliedHealth*100/9;
    }
    
    appliedHealth = Math.round(appliedHealth);
    appliedHealthBeforeSkills = Math.round(appliedHealthBeforeSkills);
  
    return {
      minHealth,
      baseHealth,
      maxHealth,
      defense,
      defenseHealth,
      exp: 220000,
      adjustedDefense,
      appliedHealthBeforeSkills,
      appliedHealth,
    };
  };
  
  export const handleNumberChange = (e, setter) => {
    const value = e.target.value.replace(/[^0-9]/g, ""); // Remove non-numeric characters
    setter(value ? Number(value) : 0);
  };
  
  export const handleNumberInput = (e) => {
    e.target.value = e.target.value.replace(/\B(?=(\d{3})+(?!\d))/g, ","); // Add commas
  };
  
  export const formatExp = (num) => {
    const units = ["", "만", "억", "조"];
    let result = "";
    let unitIndex = 0;
    
    while (num > 0 && unitIndex < units.length) {
      const part = num % 10000;
      if (part > 0) {
        result = part.toLocaleString() + units[unitIndex] + " " + result;
      }
      num = Math.floor(num / 10000);
      unitIndex++;
    }
    
    return result.trim();
  };
  
  export const calculateExp = (mode, currentHP, targetHP, currentMP, targetMP, experienceData) => {
    let expNeededHP = 0;
    let expNeededMP = 0;
    const dataHP = experienceData[mode].체력;
    const dataMP = experienceData[mode].마력;
  
    let currentHPValue = currentHP;
    let currentMPValue = currentMP;
  
    while (currentHPValue < targetHP) {
      for (const range of dataHP) {
        if (currentHPValue >= range.min && currentHPValue < range.max) {
          expNeededHP += range.per50;
          break;
        }
      }
      currentHPValue += 50;
    }
  
    while (currentMPValue < targetMP) {
      for (const range of dataMP) {
        if (currentMPValue >= range.min && currentMPValue < range.max) {
          expNeededMP += range.per25;
          break;
        }
      }
      currentMPValue += 25;
    }
  
    return { hp: expNeededHP, mp: expNeededMP };
  };
  
  export const calculateTime = (totalExp, expPerHour, expPerLap) => {
    let hours = expPerHour ? (totalExp.hp + totalExp.mp) / (expPerHour * 1e8) : 0;
    let laps = expPerLap ? (totalExp.hp + totalExp.mp) / (expPerLap * 1e8) : 0;
    return { hours, laps };
  };
  